import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http/index';
import { Table } from '../../../components';
import {
  getAllVehiclesSaga,
  editBlockVehicleStatusSaga,
} from '../../../store/actions';
// import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import AddVehicleTypeModel from './AddVehicleTypeModel';

// eslint-disable-next-line no-unused-vars
const Vehicles = props => {
  const [modelOpen, setModelOpen] = useState(false);
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [category, setCategory] = useState('');
  // eslint-disable-next-line no-unused-vars
  const [isEdit, setEditCategory] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  const {
    vehicles: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.vehicleTypes);
  // eslint-disable-next-line no-unused-vars
  const { open } = useSelector(state => state.modal);

  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllVehiclesSaga({ columnName, orderBy, search, skip: 0, limit }),
      );
    } else {
      dispatch(
        getAllVehiclesSaga({ columnName, orderBy, search, skip, limit }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const statusAction = data => {
    console.log(data);
    dispatch(editBlockVehicleStatusSaga({ id: data.id, isBlock: data.status }));
  };
  // const openEditCategoryModal = data => {
  //   setCategory(data);
  //   setEditCategory(true);
  //   setModelOpen(true);
  // };
  const tableFields = [
    {
      displayName: 'Vehicle ID',
      key: '_id',
    },
    {
      displayName: 'Picture',
      key: 'photo',
      noSort: true,
      isMedia: true,
      mediaType: 'image',
    },
    {
      displayName: 'Type',
      key: 'type',
    },
    {
      displayName: 'Fee / KM',
      key: 'feePerKM',
    },
    {
      displayName: 'Min Fare',
      key: 'minFare',
    },
    {
      displayName: 'Status',
      key: 'isBlock',
      noSort: true,
      isStatus: true,
      positiveValue: false,
      action: statusAction,
    },
    // {
    //   displayName: 'Image',
    //   key: 'thumbnail',
    //   isMedia: true,
    //   mediaType: 'image',
    //   // baseURL: `${process.env.REACT_APP_END_POINT_IMG_URL}/category/`,
    //   noSort: true,
    // },

    // {
    //   displayName: 'Status',
    //   key: 'status',
    //   isStatus: true,
    //   positiveValue: true,
    //   action: statusAction,
    // },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-pencil" aria-hidden="true" />,
          action: '/vendors/vehicles/edit-vehicle',
        },
      ],
    },
  ];

  const linkObj = {};

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Vehicle Types</h2>
              </div>
              <div className="text-right  d-flex-right">
                <Link
                  to="/vendors/vehicles/add-vehicle"
                  className="btn btn-primary m-l-sm m-b-md"
                >
                  Add Vehicle Type
                </Link>
                {/* <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  data-toggle="modal"
                  data-target="#send-modal"
                  onClick={() => {
                    setEditCategory(false);
                    setModelOpen(true);
                  }}
                >
                  Add Vehicle Type
                </button> */}
              </div>
              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  link={linkObj}
                  linkPropsName="_id"
                  statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <AddVehicleTypeModel
          setModelOpen={setModelOpen}
          tableFilter={tableFilter}
          category={category}
          isEdit={isEdit}
        />
      )}
      {/* {open && <AlertMessageModal />} */}
    </div>
  );
};

export default SetTokenHeader(Vehicles, axios);
